ser-select: none;\r\n}\r\na, .menu-icon, button {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  cursor: pointer;\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n}\r\n/* menhilangkan efek biru pada saat klik link di mobile */\r\n*, *:before, *:after {\r\n  box-sizing: border-box;\r\n  -moz-box-sizing: border-box;\r\n  -webkit-box-sizing: border-box;\r\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\r\n  -webkit-tap-highlight-color: transparent;\r\n}\r\n.title {\r\n  text-align: center;\r\n  padding: 10px 0;\r\n  text-align: center;\r\n}\r\n\r\n/* body */\r\nbody {\r\n  padding-top: 50px;\r\n}\r\n\r\n/* header */\r\nheader {\r\n  padding: 0 20px 0 10px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  position: fixed;\r\n  top: 0;\r\n  width: 100%;\r\n  background: $bg-1;\r\n  color: $text-white;\r\n  z-index: 10;\r\n}\r\nheader .header-logo, \r\nheader .menu-icon, \r\nheader .menu-container \r\n{z-index: 10;}\r\n\r\nheader .header-logo img {\r\n  object-fit: cover;\r\n  height: 50px;\r\n  width: auto;\r\n}\r\nheader .header-logo h1 {\r\n  font-size: 1rem;\r\n}\r\n\r\nheader .menu-icon {\r\n  display: none;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 50px;\r\n  font-size: 1.5rem;\r\n  color: $text-white;\r\n}\r\n\r\n@media screen and (max-width: 700px) {\r\n  header .menu-icon {\r\n    display: flex !important;\r\n    background: $bg-1;\r\n    border: none;\r\n  }\r\n  header .menu-container {\r\n    display: flex;\r\n    justify-content: flex-end;\r\n    flex-grow: 1;\r\n    width: 100%;\r\n    background: $bg-1;\r\n  }\r\n  header .nav {\r\n    top: -250px; left: 0;\r\n    width: 100%;\r\n    position: absolute;\r\n    background: $bg-1;\r\n    transition: all 0.5s ease-in-out;\r\n  }\r\n\r\n  header .nav ul {\r\n    padding-bottom: 10px;\r\n    flex-direction: column;\r\n    row-gap: 10px;\r\n  }\r\n  .open {\r\n    top: 50px !important;\r\n  }\r\n}\r\n\r\nheader .n